@import "../../SCSS/mixins";

.welcome-view {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;

  .agent-icon {
    height: 120px;
    object-fit: cover;
  }

  .agent-title {
    font-size: 24px;
    margin-top: 30px;
    color: var(--text-title);
    line-height: 20px;
  }
}
