@import "../../SCSS/mixins";

.error-message {
  text-align: center;

  .error-message-icon {
    font-size: 48px;
    color: var(--text-element);
    margin-bottom: 30px;
  }

  .error-message-description {
    font-size: 16px;
    color: var(--text-element);
    word-wrap: break-word;
  }
}
