@import "../../SCSS/mixins";

.top-head {
  position: fixed;
  width: 100%;
  display: flex;
  z-index: 2;
  top: 0;
  height: 60px;

  @media screen and (max-width: 1000px) {
    background-color: var(--background);
  }

  .top-head-container {
    padding: 12px;
    display: flex;
    align-items: center;
    flex: 1 0 0;

    .top-head-icon {
      width: 30px;
      height: 30px;
      object-fit: cover;
    }

    .top-head-info {
      display: inline-block;
      margin-left: 12px;

      .top-head-title {
        font-size: 18px;
        color: var(--text-title);
        line-height: 15px;
      }

      .top-head-subtitle {
        color: var(--text-secondary);
        font-size: 14px;

        a[href] {
          color: var(--text-primary);
          text-decoration: none;
        }
      }
    }
  }
}
