@import "../../SCSS/mixins";

.chat-field {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: var(--background);
  z-index: 2;

  .chat-field-container {
    max-width: var(--container-width);
    margin: auto auto;
    padding: 12px;
  }

  .chat-field-flexible {
    display: flex;
    border-radius: 40px;
    border: var(--border);

    .chat-field-input {
      font-size: 16px;
      width: 100%;
      box-sizing: border-box;
      border: none;
      padding: 10px 0 10px 12px;
      color: var(--text-primary);
      border-radius: 40px 0 0 40px;
      background-color: transparent;

      &:disabled {
        cursor: not-allowed;
      }
    }
  }

  .chat-field-action {
    @include reset;
    padding: 10px 12px;
    cursor: pointer;
    color: var(--accent);
    font-size: 24px;
    display: flex;

    &:disabled {
      cursor: not-allowed;
    }

    &.mic_active {
      color: #f44336;
    }
  }
}
